// import {throwError as observableThrowError,  Observable } from 'rxjs';

import {Observable} from 'rxjs';
import {_throw as throwError} from 'rxjs/observable/throw';

// import {throwError as observableThrowError} from 'rxjs/observable/throwError';


import {catchError} from 'rxjs/operators';
import {Injectable, Injector} from '@angular/core';
import {Router} from '@angular/router';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor
} from '@angular/common/http';
import {AuthenticationService} from './services/user/authentication-service.service';

@Injectable()
export class TokenInterceptor implements HttpInterceptor {
  constructor(private injector: Injector) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const auth = this.injector.get(AuthenticationService);
    const router = this.injector.get(Router);
    const headers = {'x-auth-token': `${auth.token}`};
    request = request.clone({setHeaders: headers});
    return next.handle(request).pipe(catchError((res) => {
      if (res.status === 401 || res.status === 403 || res.status === 0) {
        auth.clearToken();
        router.navigate(['/page/login']);
      } else if (res.status === 404) {
        //router.navigate(['/home']);
        return;
      } else if (res.status === 400 || res.status === 422 || res.status === 500) {
        return throwError(res);
      } else {
        router.navigate(['/home']);
      }
      return throwError(res);
    }));
  }
}
