import {ProductModel} from '../model/product-model';
import {Injectable} from '@angular/core';
import {Constants} from '../../../services/constant';
import {HttpClient} from '@angular/common/http';
import {AuthenticationService} from '../../../services/user/authentication-service.service';
import {Observable} from 'rxjs';

@Injectable()
export class ProductService {
  constructor(private httpClient: HttpClient, private authenticationService: AuthenticationService) {
  }

  saveProduct(data) {
    return this.httpClient.post(Constants.SAVE_PRODUCT_URL, data);
  }

  getAllProducts() {
    return this.httpClient.get(Constants.GET_ALL_PRODUCT_URL);
  }

  getProducts(): Observable<ProductModel[]> {
    return this.httpClient.get<ProductModel[]>(Constants.GET_ALL_PRODUCT_URL);
  }

  getProductsPrice() {
    return this.httpClient.get(Constants.GET_PRODUCTS_PRICE_URL);
  }

  getProduct(data) {
    if (!data) {
      return new Observable<Object>();
    } else {
      return this.httpClient.get(Constants.GET_ALL_PRODUCT_URL + '/' + data);
    }
  }

  getAllCaterogys() {
    return this.httpClient.get(Constants.GET_ALL_CATEGORY_URL);
  }

  getProductByBarcode(barcode) {
    return this.httpClient.get<ProductModel>(Constants.GET_PRODUCT_BY_BARCODE_URL + '/' + barcode);
  }

  saveOrders(data) {
    return this.httpClient.post(Constants.SAVE_ORDERS_URL, data);
  }

  saveOrdersV2(data) {
    return this.httpClient.post(Constants.SAVE_ORDERS_V2_URL, data);
  }

  updateOrders(data) {
    return this.httpClient.put(Constants.UPDATE_PRODUCT_URL, data);
  }

  updatePrice(data) {
    return this.httpClient.put(Constants.UPDATE_PRICE_PRODUCT_URL, data);
  }

  printReceipt(data) {
    return this.httpClient.post(Constants.POST_PRINT_RECEIPT, data);
  }
}
